'use strict'

var utils = require('./utils')
var XEProgress = require('../handle/progress')
var XEAbortController = require('../handle/abortController')
var XERequest = require('../handle/request')
var fetchRequest = require('../adapters/fetch')
var sendJSONP = require('../adapters/jsonp')
var setupDefaults = require('./setup')
var handleExports = require('../handle')
var interceptorExports = require('../handle/interceptor')

var errorType = {
  ERR_A: 'The user aborted a request.',
  ERR_T: 'Request timeout.',
  ERR_F: 'Network request failed.'
}

/**
  * : nodejsbrowser
  *
  * @param { Object} options
  * @return { Promise }
  */
function XEAjax (options) {
  var opts = utils.assign({}, setupDefaults, { headers: utils.assign({}, setupDefaults.headers) }, options)
  var request = new XERequest(opts)
  var XEPromise = request.$Promise || Promise
  return new XEPromise(function (resolve, reject) {
    return interceptorExports.requests(request).then(function () {
      (request.jsonp ? sendJSONP : fetchRequest)(request, function (response) {
        interceptorExports.toResolves(request, handleExports.toResponse(response, request), resolve, reject)
      }, function (type) {
        interceptorExports.toRejects(request, new TypeError(errorType[type || 'ERR_F']), resolve, reject)
      })
    })
  }, request.$context)
}

XEAjax.interceptors = interceptorExports.interceptors
XEAjax.serialize = utils.serialize
XEAjax.Progress = XEProgress
XEAjax.AbortController = XEAbortController

/**
 * Installation
 */
XEAjax.use = function (plugin) {
  plugin.install(XEAjax)
}

/**
 * 
 *
 * 
 * @param { String } url 
 * @param { String } baseURL 
 * @param { String } method (GET)
 * @param { Object } params url
 * @param { Object } body 
 * @param { String } bodyType json-data,form-data(json-data)
 * @param { String } jsonp jsonp,callback
 * @param { Number } timeout 
 * @param { Object } headers 
 * @param { Function } transformParams(params, request) URL
 * @param { Function } paramsSerializer(params, request) URL
 * @param { Function } transformBody(body, request) 
 * @param { Function } stringifyBody(body, request) 
 * @param { Function } validateStatus(response) 
 *  fetch 
 * @param { String } cache ,default,no-store,no-cache,reload,force-cache,only-if-cached(default)
 * @param { String } credentials  cookie ,: omit,same-origin,include(same-origin)
 * @param { String } referrer : no-referrer,clientURL(client)
 * @param { String } referrerPolicy : no-referrer,no-referrer-when-downgrade,origin,origin-when-cross-origin,unsafe-url
 * @param { String } integrity subresource integrity
 * ())
 * @param { Function } $XMLHttpRequest  XMLHttpRequest 
 * @param { Function } $http  http 
 * @param { Function } $fetch  fetch 
 * @param { Function } $jsonp  jsonp 
 * @param { Function } $Promise  Promise 
 * @param { Function } $context 
 * @param { Function } $options 
 */
XEAjax.setup = function (options) {
  utils.assign(setupDefaults, options)
}

module.exports = XEAjax
